// Typography
//---------------------------------------------

body {
	@extend %base-style;
}

a,
button,
input:focus,
[type="button"],
[type="reset"],
[type="submit"],
textarea:focus,
.button
.gallery img {
	transition: all 0.1s ease-in-out;
}

a {
	color: $default-base;
	border-bottom: 1px dotted;
	font-weight: 700;
	text-decoration: none;
	word-wrap: break-word;

	&:hover {
		border-bottom: 0;
		color: $black;
	}

	img {
		border-bottom: 0;
	}
}

p {
	margin: 0 0 $base-margin/2;
	padding: 0;

	&.has-background {
	    padding: $base-padding;
	}

	&.alignfull,
	&.alignwide {
		@extend %box-shadow;
	}
}

strong {
	font-weight: 700;
}

ol,
ul {
	margin: 0;
	padding: 0;
}

pre {
	background: $black;
	color: $white;
	padding: 10px;
	overflow: scroll;
}

code {
	background-color: $black;
	color: $white;
}

address {
	margin-bottom: $base-margin/2;
}

mark {
	background-color: $default-accent;
	color: $default-accent-contrast;
}

// Blockquote
//---------------------------------------------

blockquote,
.wp-block-quote,
.wp-block-pullquote {
	color: $default-accent;
	margin: $base-margin 0;
	padding: $base-padding/2 0;

	@media screen and (min-width: $small-screen) {
		margin: $base-margin;
		padding: $base-padding;
	}

	cite,
	footer {
	    display: block;
	    margin-top: $base-margin/2;
	    font-size: $base-font-size - 2;
	}

	&.wp-block-quote {
		border-left: 4px solid $black;
		padding: 0 0 0 $base-padding;
	}

	&.wp-block-pullquote {
		cite {
			text-transform: none;
		}
	}

	&.is-large {
		border: none;
		margin: $base-margin 0;
		padding: 0;

		p {
		    font-size: $base-font-size * 2;
		}

		cite,
		footer {
		    font-size: $base-font-size;
		}
	}

}

// Horizontal Rule
//---------------------------------------------

hr,
hr.wp-block-separator,
.clear-line,
img.wp-more-tag {

	border: none;
	border-top: 1px solid $black;
	height: 1px;
	margin: $base-margin 0;
	max-width: 100%;
	overflow: hidden;
	width: 100%;
}

// Headings
//---------------------------------------------

h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: $heading-font-family;
	font-weight: 700;
	line-height: $heading-line-height;
	margin: 0 0 $base-margin/2;
}

h1 {
	font-size: $h1-font-size;
}

h2 {
	font-size: $h2-font-size;
}

h3 {
	font-size: $h3-font-size;
}

h4 {
	font-size: $h4-font-size;
}

h5 {
	font-size: $h5-font-size;
}

h6 {
	font-size: $h6-font-size;
}

// Objects
//---------------------------------------------

embed,
iframe,
img,
object,
video {
	max-width: 100%;
}

img {
	height: auto;
	width: auto; /* IE8 */
}

// Tables
//---------------------------------------------

table {
	border-collapse: collapse;
	border-spacing: 0;
	font-size: .8em;
	line-height: 2;
	margin-bottom: $base-margin;
	width: 100%;
	word-break: break-all;
}

th,
td {
	border-top: 1px solid lighten($black, 75%);
	padding: 5px;
	text-align: left;
}

thead,
tfoot {
	background-color: lighten($black, 85%);
	font-weight: 700;

	td {
		border: 0;
	}
}

// Alignments
//---------------------------------------------

.alignleft {
	display: inline;
	float: left;
	margin-right: $base-margin/2;
	text-align: left;
}

.alignright {
	display: inline;
	float: right;
	margin-left: $base-margin/2;
	text-align: right;
}

.aligncenter {
	clear: both;
	display: block;
	margin: 0 auto;
}

.alignfull,
.alignwide {

	width: 100vw;
	position: relative;
	left: 50%;
	right: 50%;
	margin-left: -50vw;
	margin-right: -50vw;
	text-align: center;

	img {
		@extend %box-shadow;
	}
}

.alignfull img {
	width: 100%;
}

.alignwide img {
	max-width: $max-width;
}
